@import url(//fonts.googleapis.com/earlyaccess/opensanshebrew.css);
@import url(//fonts.googleapis.com/earlyaccess/opensanshebrew.css);
* {
  box-sizing: border-box;
}
html {
  font-family: "Open Sans Hebrew", sans-serif;
  line-height: 1.6;
  height: 100%;
}

body {
  transition: all 0.3s;
  color: #62646a;
  font-size: 16px;
  margin: 0;
  /*height: 100%;*/
  font-family: "Open Sans Hebrew", sans-serif;
}


h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  line-height: 1;
  color: black;
}

p {
  margin: 0;
}

a {
  text-decoration: none;
  color: black;
}
a:hover {
  text-decoration: none;
}
input:focus {
  outline: none;
}
button:focus {
  outline: none;
}

img {
  width: 150px;
}

ul {
  padding: 0;
  list-style-type: none;
}

::-webkit-scrollbar {
  width: 8px;
  height: 8px;
  background-color: #d0d0d0;
  border-radius: 15px;
}

* {
  padding: 0;
  margin: 0;
  outline: none;
  font-family: "Open Sans Hebrew", sans-serif;
}

body {
  background-color: #e7ecf7;
  padding: 0 !important;
}

ul {
  list-style-type: none;
  display: block;
}
.button {
  font-family: Open Sans, sans-serif;
  font-weight: 400;
  border-radius: 15px;
  border-radius: var(--border-radius);
  border: 0;
}
a {
  text-decoration: none;
  background-color: transparent;
}

:root {
  --main-prop-color: #001145;
  --yellow: #f7c324;
  --negative-red-color: #a91e2c;
  --positive-green-color: #18634b;
  --border-radius: 15px;
  --br-bottom: 0 0 15px 15px;
  --br-top: 15px 15px 0 0;
  --chart-axix-color: #666;
  --tooltipAxisSize: 30px;
  --text-color-lineChart: #c0c8d3;
  --font-size-small: 15px;
  --font-size-medium: 18px;
  --font-size-large: 24px;

  --font-12: 12px;
  --font-12-8: 12.8px;
  --font-13: 13px;
  --font-14: 14px;

  --marginSpacing: 24px;

  --dark-theme-line-blue: #4286e6;
}

canvas.mapboxgl-canvas {
  border-radius: 15px;
  border-radius: var(--border-radius);
}


.mystyle::after {
	content: "Aaaaaaaaaaaaa";
	position: absolute;
	left: -230px;
	color: #ffffff94;
	font-size: 50px;
	-webkit-transform: rotate(-45deg);
	        transform: rotate(-45deg);
	font-weight: 600;
}

